import React from 'react';

import Icon from '../icon';

import './style.css';

class IpShowcase extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const {title, subTitle, classes, style} = this.props;

    return (
      <div className="ip-showcase">
        <div className={`ip-showcase-background ${style}`}>
          <div className={`ip-showcase-background-title ${style}`}>
            <div className="ip-showcase-background-title-text">
              <h1>{title}</h1>
              {
                subTitle ? <p>{subTitle}</p> : null
              }
            </div>
          </div>
          <Icon className="ip-showcase-icon" color="#fff" size={60} name={style === 'style1' ? 'circle-right' : 'circle-left'}/>
          <div className={`ip-showcase-background-img ${classes}`}></div>
        </div>
      </div>
    );
  }
}

export default IpShowcase;